<template>
  <div class="article_container">
    <!-- 头部 -->
    <van-nav-bar class="topbar-container" title="文章详情">
      <van-icon
        slot="left"
        name="arrow-left"
        @click="
          $router.push({
            name: `Home`,
            params: { active },
          })
        "
      />
      <van-icon slot="right" name="ellipsis" size="24" />
    </van-nav-bar>
    <!-- 文章内容 -->
    <van-list class="article_body">
      <h2 class="title">{{ articleInfo.title }}</h2>
      <van-cell :border="false" class="article_author" center>
        <van-image
          class="avatar"
          slot="icon"
          round
          fit="cover"
          :src="articleInfo.aut_photo"
        />
        <div slot="title" class="author_name">{{ articleInfo.aut_name }}</div>
        <div slot="label" class="article_time">
          {{ articleInfo.pubdate | formatTime }}
        </div>

        <van-button
          class="follow_btn"
          size="small"
          round
          :icon="articleInfo.is_followed ? `` : `plus`"
          type="info"
          :loading="isLoading"
          @click="followUser"
          >{{ articleInfo.is_followed ? "取消关注" : `关注` }}</van-button
        >
      </van-cell>
      <div
        class="markdown-body"
        v-html="articleInfo.content"
        ref="article_content"
      ></div>
      <p class="article_end">
        - - - - - - - - - - - - - - 正文结束 - - - - - - - - - - - - - -
      </p>

      <comment-list
        title=" 全部评论"
        :source="articleId"
        :list="commentList"
        v-model="totalComments"
        @onReplay="replayClick"
      ></comment-list>
    </van-list>
    <!-- 底部 -->
    <div class="article_btm">
      <van-button
        class="comment_btn"
        type="default"
        round
        size="small"
        @click="isWriteShow = true"
        >写评论</van-button
      >
      <van-icon name="comment-o" :badge="totalComments" />
      <van-icon
        :name="articleInfo.is_collected ? `star` : `star-o`"
        :color="articleInfo.is_collected ? `orange` : `#777`"
        @click="onCollect"
      />
      <van-icon
        :name="articleInfo.attitude == 1 ? `good-job` : `good-job-o`"
        :color="articleInfo.attitude == 1 ? `red` : `#777`"
        @click="onLiking"
      />
      <van-icon name="share" />
    </div>
    <!-- 发布评论弹出层 -->
    <van-popup class="write_place" v-model="isWriteShow" position="bottom">
      <comment-post
        @postSuccess="onPostSuccess"
        :target="articleId"
      ></comment-post>
    </van-popup>
    <!-- 回复评论弹出层 -->
    <van-popup class="write_place" v-model="isReplayComment" position="bottom">
      <replay-comment
        :articleId="articleId"
        v-if="isReplayComment"
        @close="isReplayComment = false"
        :comment="replayComment"
      ></replay-comment>
    </van-popup>
  </div>
</template>

<script>
import './mardown.css';
import { getArticleInfo, followAuthor, deleteFollow } from '@/api/home.js';
import { ImagePreview } from 'vant';
import { getItem } from '@/utils/storage.js';

import {
  addCollected,
  deleteCollected,
  addLiking,
  deleteLiking,
} from '@/api/article.js';

export default {
  name: 'ArticleInfo',
  components: {
    CommentList: () => import('./component/comment.vue'),
    CommentPost: () => import('./component/comment_post.vue'),
    ReplayComment: () => import('./component/replayComment.vue'),
  },
  props: {
    articleId: {
      // 文章id
      type: [String, Number, Object],
      required: true,
    },
  },
  data() {
    return {
      active: getItem('active'),
      totalComments: 0, // 文章总评论数
      articleInfo: {}, // 文章详情数据
      isLoading: false, // 关注按钮是否在加载
      isWriteShow: false, // 评论框是否显示
      isReplayComment: false, // 回复评论框是否显示
      commentList: [], // 评论列表
      replayComment: {},
    };
  },
  methods: {
    // 获取文章信息
    async loadArticle() {
      try {
        const { data } = await getArticleInfo(this.articleId);

        this.articleInfo = data.data;

        this.$nextTick(() => {
          this.handleImg();
        });
      } catch (e) {
        console.log(e);
      }
    },
    // 等待请求后台文章内容数据的更新 触发dome更新时调用
    handleImg() {
      const content = this.$refs.article_content;
      const imgList = content.querySelectorAll('img');
      const srcList = [];
      imgList.forEach((item, index) => {
        srcList.push(item.src);
        item.addEventListener('click', () => {
          ImagePreview({
            images: srcList,
            closeable: true,
            startPosition: index,
            showIndicators: true,
          });
        });
      });
    },
    // 关注按钮点击
    async followUser() {
      this.isLoading = true;
      const id = this.articleInfo.aut_id;
      try {
        if (this.articleInfo.is_followed) {
          const res = await deleteFollow(id);
        } else {
          const res = await followAuthor(id);
        }
        this.articleInfo.is_followed = !this.articleInfo.is_followed;
        this.isLoading = false;
      } catch (e) {
        console.log(e);
      }
    },
    // 收藏按钮
    async onCollect() {
      try {
        if (this.articleInfo.is_collected) {
          await deleteCollected(this.articleId);
        } else {
          await addCollected(this.articleId);
        }
        this.$toast.success({
          message: this.articleInfo.is_collected ? '已取消收藏' : '收藏成功',
          forbidClick: true,
        });
        this.articleInfo.is_collected = !this.articleInfo.is_collected;
      } catch (e) {
        console.log(e);
      }
    },
    // 点赞按钮
    async onLiking() {
      try {
        if (this.articleInfo.attitude == 1) {
          await deleteLiking(this.articleId);
          this.articleInfo.attitude = -1;
        } else {
          await addLiking(this.articleId);
          this.articleInfo.attitude = 1;
        }
        this.$toast.success({
          message: this.articleInfo.attitude == 1 ? '已点赞' : '已取消点赞',
          forbidClick: true,
        });
      } catch (e) {
        console.log(e);
      }
    },
    // 文章评论成功回调
    onPostSuccess(comment) {
      this.totalComments += 1;
      this.commentList.unshift(comment);
      this.isWriteShow = false;
    },
    // 点击回复 获取当前评论对象
    replayClick(comment) {
      this.replayComment = comment;
      this.isReplayComment = true;
    },
  },
  created() {
    this.loadArticle();
  },
  activated() {
    // this.loadArticle();
  },
};
</script>

<style lang="less" scoped>
.article_container {
  .topbar-container {
    height: 46px;
    /deep/.van-nav-bar__title {
      font-size: 14px;
    }
  }
  .write_place {
    box-sizing: border-box;
    padding: 6px;
  }
}
.article_body {
  position: fixed;
  left: 0;
  right: 0;
  top: 46px;
  bottom: 40px;
  padding: 12px 15px 0;
  background-color: #fff;
  overflow: scroll;
  .title {
    margin: 0;
    font-size: 21px;
    color: #3a3a3a;
  }
  .article_author {
    padding-left: 0;
    padding-right: 0;
    margin-top: 10px;
    .avatar {
      width: 36px;
      height: 36px;
      margin-right: 6px;
    }
    /deep/.van-cell__title {
      flex-direction: column;
      justify-content: center;
      .author_name {
        font-size: 12px;
        color: #333;
      }
      .article_time {
        font-size: 12px;
        color: #b4b4b4;
      }
    }
    .follow_btn {
      width: 88px;
      height: 29px;
    }
  }
  .article_end {
    padding: 20px 0;
    text-align: center;
    font-size: 12px;
    color: #ccc;
  }
}
.article_btm {
  position: absolute;
  bottom: 0;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 40px;
  padding: 0 15px;
  border-top: 1px solid #ccc;
  background: #fff;
  font-size: 16px;
  .comment_btn {
    font-size: 13px;
    height: 24px;
    line-height: 24px;
    padding: 0 40px;
    color: #777;
  }
  .van-icon {
    color: #777;
    .van-info {
      font-size: 12px;
      font-weight: inherit;
    }
  }
}
</style>
