import { request } from './index';

// 获取用户频道列表
export const getUserChannels = function () {
  return request({
    url: '/app/v1_0/user/channels',
  });
};

// 获取用户频道推荐
export const getUserArticles = function (params) {
  return request({
    url: '/app/v1_1/articles',
    params,
  });
};

// 获取所有频道
export const getAllChannels = function () {
  return request({
    url: '/app/v1_0/channels',
  });
};

// 修改用户频道
export const editUserChannels = function (data) {
  return request({
    method: 'patch',
    url: '/app/v1_0/user/channels',
    data,
  });
};

// 删除用户频道
export const deleteUserChannels = function (id) {
  return request({
    method: 'delete',
    url: `/app/v1_0/user/channels/${id}`,
  });
};

// 获取文章详情
export const getArticleInfo = function (articleId) {
  return request({
    url: `/app/v1_0/articles/${articleId}`,

  });
};

// 关注用户
export const followAuthor = function (userId) {
  return request({
    method: 'post',
    url: '/app/v1_0/user/followings',
    data: { target: userId },
  });
};

// 取消关注
export const deleteFollow = function (userId) {
  return request({
    method: 'delete',
    url: `/app/v1_0/user/followings/${userId}`,

  });
};
