import { request } from './index';

// 收藏文章
export const addCollected = function (id) {
  return request({
    method: 'post',
    url: '/app/v1_0/article/collections',
    data: {
      target: id,
    },
  });
};

// 取消收藏文章
export const deleteCollected = function (id) {
  return request({
    method: 'delete',
    url: `/app/v1_0/article/collections/${id}`,
  });
};

